<template>
  <div :class="{'tree-menu':true,}">
    <li v-if="data.menuType !== 2" :class="{'first-floor': data.level === 1,'second-floor': data.level === 2,'third-floor': data.level === 3}">
      <span @click.stop="toggle" class="content-box">
        <span class="title">{{ data.menuName }}</span>
        <img v-if="hasChild" :class="{'animation':open}" :src="`${$cdnImageUrl}/tableau/arrowShrink.png`" alt="" />
        <!-- <img v-if="hasChild && open" :src="`${$cdnImageUrl}/tableau/arrowSpread.png`" alt="" /> -->
        <span v-if="!hasChild" class="icon file-text"></span>
      </span>
      <ul v-show="open" v-if="hasChild">
        <Tree-item ref='children' v-for="(value, index) in data.list" :data="value" :key="index" :nodeClick='nodeClick'></Tree-item>
      </ul>
    </li>
    <li v-else>
        <span @click="getRequest(data.menuLinks,data.menuName)" class="view-menu">{{data.menuName}}</span>
    </li>
  </div>
</template>
<script>
export default {
  name: 'TreeItem',
  props: {
    data: {
      type: [Object, Array],
      required: true,
    },
    nodeClick: {
      type: Function,
      required: true,
    }
  },
  data() {
    return {
      open: false,
    }
  },
  created() {},
  mounted() {

  },
  computed: {
    hasChild() {
      return this.data.list && this.data.list.length
    },
  },
  methods: {
    toggle() {
      if (this.hasChild) {
        this.open = !this.open
      }
      // 父级收起，其所有展开的子级自动收起
      if (!!this.$refs.children && this.$refs.children.length > 0) {
        this.$refs.children.forEach((item) => {
          item.open = false
        })
      }
    },
    getRequest(value, name) {
      this.nodeClick(value, name)
      // this.$emit('selectmenufather', { value, name })
      // this.$emit('selectmenu', { value, name })
      // this.$store.commit('tableau/setIfranmSource', value)
      // this.$store.commit('tableau/setMenuName', name)
    },
  },
}
</script>
