<template>
  <div class="tableau_box">
    <Header>
      <div slot="left" class="goBackIcon"><i class="icon-back"></i></div>
      <div class="tab-bar" slot="title" @click="toShow">
        <div class='tableau_title'>{{selectValue}}</div>
        <span class="son"></span>
      </div>
      <div class="order-header-right" slot="right">
        <span @click="toShow">切换菜单</span>
      </div>
    </Header>
    <van-popup v-model="show" position="right" :style="{ height: '100vh', width: 'calc(100% - 2.4rem)'}" >
      <ul v-for="(item,index) in data" :key='index'>
        <Menu :data='item' :nodeClick='nodeClick'></Menu>
      </ul>
    </van-popup>
    <div style="marginTop: 0.93rem;height: 94vh">
    <iframe
      :src="ifranmSrc"
      frameborder="0"
      height='100%'
      width='100%'
      marginheight='0.93rem'
     ></iframe>
     </div>
  </div>
</template>

<script>
import { tableauMenu, getStoreMenuDefaultView, getStoreTableauTicket } from '@/http/service/tableau'
import { myManageinfo } from '@/http/service/mine'
import Header from '@/components/header'
import Menu from './components/menu.vue'
export default {
  components: {
    Header,
    Menu,
  },
  data() {
    return {
      ifranmSrc: '',
      selectValue: '首页',
      show: false,
      data: [],
      ticked: null, // 票据
      childeUrl: '',
    }
  },
  created() {
    this.initialize()
  },
  mounted() {
    this.getMenu()
  },
  methods: {
    /**
     * 展示侧边弹窗
     */
    toShow() {
      this.show = true
    },
    /**
     * 获取菜单树
     */
    getMenu() {
      tableauMenu().then(res => {
        if (res.data.success) {
          this.data = res.data.data
          this.data.sort(function(a, b) { return a.menuSort - b.menuSort })
        }
      })
    },
    /**
     * 初始化默认视图
     */
    initialize() {
      getStoreMenuDefaultView().then(res => {
        this.rewrite(res.data.data.menuLinks).then(res => {
          this.ifranmSrc = res
          console.log('------初始化--------', res)
        })
        this.selectValue = res.data.data.menuName
      }).catch(res => {
        this.$toast('获取看板数据异常，请稍后重试');
      })
    },
    /**
     * 获取店铺票据
     */
    async getStoreTableauTicket() {
      // 获取88码
      let result = await myManageinfo().then(res => {
        return getStoreTableauTicket({ storeCode: res.data.data.storeCode }).then(res => {
          if (res.data.success) {
            return res.data.data
          }
        }).catch(res => {
          this.$toast('获取票据异常');
        })
      })
      return result
    },
    /**
     * 根据票据重写url
     */
    async rewrite(oldUrl) {
      let ticked = null
      let newURL = await
      this.getStoreTableauTicket().then(res => {
        ticked = res
        console.log('-----ticked--------', res)
        let domain = oldUrl.split('/'); // 以“/”进行分割
        let lastUrl = '' // 域名后的路径拼接
        let centerArr = domain.slice(3)
        centerArr.forEach(item => {
          lastUrl += '/' + item
        })
        return domain[0] + '//' + domain[2] + '/trusted/' + ticked + lastUrl
      })
      return newURL
    },
    /**
     * 菜单选择请求
     */
    nodeClick(value, data) {
      console.log('----nodeclick----', value, data)
      this.selectValue = data
      this.rewrite(value).then(res => {
        this.ifranmSrc = res
        console.log('------菜单选择--------', res)
      })
    },
  },
}
</script>

