/**
 *  获取数据看板tableau
 */

import http from '../http'
import url from '../url'

export const tableauMenu = (params) => {
  return http.request({
    url: url.tableauMenu,
    method: 'get',
    params
  })
}

export const getStoreMenuDefaultView = (params) => {
  return http.request({
    url: url.getStoreMenuDefaultView,
    method: 'get',
    params
  })
}

export const getStoreTableauTicket = (params) => {
  return http.request({
    url: url.getStoreTableauTicket,
    method: 'get',
    params
  })
}
