/**
 * 分类模块
 * 导出至'@/http/service/category'
 */
import http from '../http'
import url from '../url'

// 个人中心信息
export const myManageinfo = (data, noLogin) => {
  return http.request({
    url: url.myManage,
    method: 'get',
    params: { '_t': Date.now() },
    noLogin
  })
}
// 个人中心信息
export const getOrderCount = data => {
  return http.request({
    url: url.getOrderCount,
    method: 'get',
    params: {
      chId: 60
    }
  })
}

// 个人中心 - 钱包信息
export const getWallet = data => {
  return http.request({
    url: url.wallet,
    method: 'get',
    params: {}
  })
}

// 个人中心 - 营收模块 判断绑卡
export const checkBindByType = params => {
  return http.request({
    url: url.checkBindByType,
    method: 'get',
    params
  })
}

// 个人中心 - 营收模块 信息汇总
export const getComminsionSumInfo = (data) => {
  return http.request({
    url: url.getComminsionSumInfo,
    method: 'POST',
    data,
    headers: {
      'Content-Type': 'application/json'
    }
  })
}

// 个人中心 - 营收模块 账单列表
export const getGoodsAmountList = (data) => {
  return http.request({
    url: url.getGoodsAmountList,
    method: 'POST',
    data,
    headers: {
      'Content-Type': 'application/json'
    }
  })
}

// 个人中心 - 营收模块 服务费信息
export const getServiceCharge = (params) => {
  return http.request({
    url: url.getServiceCharge,
    method: 'get',
    params
  })
}
